import {
	apiBaseUrl as baseUrl
} from "@/common/url_config.js"
import store from '@/store/index.js'
let flag = 0
export function request(url, params = {}, methods = "post", showLoading = true, is_form_params = false) {
	let user_info = uni.getStorageSync('user_info');
	let reqheader = {
		'Content-Type': 'application/json',
	};
	if (is_form_params) {
		// reqheader['Content-Type'] = "multipart/form-data"
		reqheader['Content-Type'] = "application/x-www-form-urlencoded"
	}
	if (user_info.token) {
		reqheader.authorization = "wx " + user_info.token;
	}
	if (showLoading) {
		uni.showLoading({
			mask: true,
			title: '...'
		})
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: baseUrl + url + '/',
			data: params,
			method: methods,
			header: reqheader,
			success(res) { //  404500 api
				let data = res.data
				if (res.statusCode == 200) { //  200
					resolve(data)
					setTimeout(() => {
						flag = 0
					}, 800)
				} else { // 404 500

					let str = ""
					if (res.statusCode == 422 || res.statusCode == 404) { // 
						str = data.data || data.message
					} else if (res.statusCode == 403) {
						if (flag == 0) {
							flag++
							uni.removeStorageSync("user_info")
							store.dispatch("forceLogin")
							setTimeout(() => {
								flag = 0
							}, 800)
							reject(res)
							return
						}
					}
					uni.showToast({
						title: str,
						icon: "none"

					})

					reject(res)
				}
			},
			fail(res) { // () 
				if (res.errMsg.indexOf("timeout") != -1) { // 
					uni.showToast({
						title: "",
						icon: "none"

					})
				} else { // 
					uni.showToast({
						title: "",
						icon: "none"

					})
				}
				reject(res)
			},
			complete() {
				showLoading && uni.hideLoading()
			}
		})
	})
}
